.contact-us {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    color: #fff;
    background-color: #121212;
  }
  
  .contact-us h1 {
    font-size: 2em;
    margin-bottom: 10px;
  }
  
  .contact-us h2 {
    font-size: 1.5em;
    margin-bottom: 20px;
  }
  
  .contact-us p {
    font-size: 1em;
    margin-bottom: 20px;
  }
  
  .contact-us a {
    color: #1e88e5;
  }
  
  .contact-us form {
    display: flex;
    flex-direction: column;
  }
  
  .contact-us label {
    margin-bottom: 15px;
    font-size: 1em;
  }
  
  .contact-us textarea,
  .contact-us input[type="email"],
  .contact-us input[type="file"] {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    background-color: #333;
    color: #fff;
    border: 1px solid #555;
    border-radius: 5px;
  }
  
  .contact-us textarea {
    height: 100px;
  }
  
  .contact-us small {
    margin-bottom: 15px;
    font-size: 0.8em;
    color: #ccc;
  }
  
  .contact-us button {
    padding: 10px 20px;
    background-color: #1e88e5;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .contact-us button:hover {
    background-color: #1565c0;
  }
  